import React, { useEffect, useState } from "react";
import { FunnelChart } from "react-funnel-pipeline";
import "react-funnel-pipeline/dist/index.css";
import LeadServices from "./../services/LeadService";
import { CustomLoader } from "../Components/CustomLoader";

export const Home = () => {
  const [funnelData, setFunnelData] = useState([]);
  const [open, setOpen] = useState(false);

  useEffect(() => {
    getAllTaskDetails();
  }, []);

  const getAllTaskDetails = async () => {
    try {
      setOpen(true);
      const response = await LeadServices.getLeadDashboard();
      console.log("response", response);
      setFunnelData(response.data);
      setOpen(false);
    } catch (err) {
      setOpen(false);
      console.log("err", err);
    }
  };

  const handleRowClick = (row) => {
    console.log("Clicked on row", row);
  };

  const getRowStyle = (index) => {
    let style = {};
    const data = funnelData[index];
    const value = data && data.value;
    const label = data && data.name;
    if (index === 4) {
      style = {
        backgroundColor: "#6d4c41",
        color: "#fff",
      };
    }
    return {
      ...style,
      height: "40px",
      transition: "all 0.2s ease-in-out",
    };
  };

  const Data = [
    { value: funnelData.new, name: "New" },
    { value: funnelData.open, name: "Open" },
    { value: funnelData.potential, name: "Potential" },
    { value: funnelData.opportunity, name: "Opportunity" },
    { value: funnelData.not_interested, name: "Not Interested" },
    { value: funnelData.converted, name: "Converted" },
    { value: funnelData.total, name: "Total" },
  ];

  const tooltipContent = (index, value) => {
    const data = value[index];
    console.log("data", data);
    const labelProp = data && data.name;
    const valueProp = data && data.value;
    console.log("valueProp", valueProp);
    console.log("labelProp", labelProp);

    return (
      <div style={{ backgroundColor: "#fff", padding: "10px" }}>
        <div style={{ fontWeight: "bold" }}>{labelProp}</div>
        <div>{valueProp}</div>
      </div>
    );
  };

  const chartContainerStyle = {
    margin: "20px",
    borderRadius: "10px",
    boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
    backgroundColor: "#fff",
    padding: "20px",
    width: "600px",
    height: "400px",
  };

  const funnelChartStyle = {
    border: "1px solid #ccc",
    borderRadius: "10px",
  };

  const segmentHoverStyle = {
    transform: "scale(1.1)",
    transition: "all 0.2s ease-in-out",
    backgroundColor: "#ccc",
    cursor: "pointer",
  };

  const textStyle = {
    fontSize: "12px",
    fontWeight: "bold",
    fontFamily: "Arial",
    color: "#333",
  };

  return (
    <>
      <CustomLoader open={open} />
      <div style={chartContainerStyle}>
        <div style={{ ...textStyle, marginBottom: "20px" }}>Lead Dashboard</div>
        <FunnelChart
          data={Data}
          onRowClick={handleRowClick}
          chartWidth={400}
          getRowStyle={getRowStyle}
          tooltipContent={tooltipContent}
          textStyle={textStyle}
          style={funnelChartStyle}
          showNames={false}
        />
      </div>
    </>
  );
};
